body {
    font-family: sans-serif;
    line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

ul,
ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

.Container {
    display: flex;
    flex-direction: column;
    margin: 20px 15px 20px 15px;
}

.MoviesLink {
    text-decoration: none;
    color: black;
    display: inline-block;
    max-width: 350px;
}

.MoviesLink:hover {
    color: orange
}
.Title {
    text-align: center;
}